
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

import ActionSheet from '@/components/ActionSheet/actionsheet'
import commonFn from '@/assets/js/DictionaryTableFn.js';
export default {
name: 'FileLogin',
components: {
XHeader,Group,Calendar,PopupPicker,Datetime,ActionSheet,XInput
},
beforeRouteEnter(to,from,next){
if(from.name == 'doctorAppointment'){
localStorage.setItem('showType' ,'A');
}else{
localStorage.setItem('showType' ,'B');
}
next();
},
data(){
return{
showA: false,  // 从就诊人过来的页面
showB: false,	// 从就诊卡过来的页面
JZlist: [], //就诊人列表
cardTypeA:'',  // 证件类型
hospitalA:'',  // 医院
state: false,
username:'', // 就诊人名称
usercode:'', // 就诊人编码
idcard:'',
zjType:[['居民身份证','居民户口簿','护照','军官证(士兵证)','驾驶执照','港澳居民来往内地通行证','台湾居民来往内地通行证','其他']],
cardTypeB:[],
hospitalB:[],
list:[['男','女','未知性别','未说明性别']],
cradNumber: '',		// 就诊卡号
PatientsInfo:'', // 就诊人信息
hospitalBM: '',	//	医院编码
hospitalList:[],	// 医院列表
BMObj:{},

jzrInfo:  {},
fromJzr: false,
}

},
mounted(){
if(window.localStorage.showType == 'A'){
this.showA = true;
}else{
this.showB = true;
this.qryPatients(); 
this.qryHos(); 
}
if(this.$route.params.PatientsInfo && this.showA){
this.PatientsInfo = this.$route.params.PatientsInfo;
this.username = this.PatientsInfo.CHZXM;  
this.usercode = this.PatientsInfo.CBM;  
this.cardTypeA = this.PatientsInfo.CZJLXMC;	
this.idcard = this.PatientsInfo.CZJHM;	
this.hospitalA = this.$route.params.hospital;	
this.hospitalBM = this.$route.params.hospitalBM;	
} else {

this.jzrInfo = JSON.parse(window.localStorage.getItem('jzrInfo')) || {};
this.fromJzr = this.jzrInfo.CBM.length > 0 ? true : false;
}
},
methods: {
qryPatients(){  // 获取就诊人列表
this.$vux.loading.show({
text: '加载中...'
})
this.$ajax.post('',{
"MSH.1": "visitor",
"MSH.2": "visiterList",
token:this.$root.loginInfo.token, //token
pageIndex:1,
pageSize:100,
}).then(res => {
console.log(res)
this.$vux.loading.hide();
if(res && res.rows){
res.rows.forEach(item=> {
this.JZlist.push({
name: item.CHZXM,
key: item,
});
});
}
}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},
qryHos(){  // 查询医院
this.$vux.loading.show({
text: '加载中...'
})
this.$ajax.post('',{
"MSH.1": "hospitalregistration",
"MSH.2": "listHospital",
token:this.$root.loginInfo.token, //token
pageIndex:1,
pageSize:1000,
}).then(res => {
this.$vux.loading.hide();
let arr = [];
if(res && res.rows){
res.rows.forEach(item=> {
arr.push(item.CMC);
this.BMObj[item.CMC] = item.CBM;
});
this.hospitalList.push(arr);
}

}).catch(error => {
console.log(error);
this.$vux.loading.hide();
})
},
select(val){   // 选择就诊人
this.username = val.name;
this.usercode = val.key.CBM;
},

ADD(){
let cardType;
if(this.showA){
cardType = this.cardTypeA;
}else{
cardType = this.cardTypeB[0]
this.hospitalBM = this.BMObj[this.hospitalB[0]];
}
this.$ajax.post('',{
"MSH.1": "visitcard",
"MSH.2": "addVisitCard",
token:this.$root.loginInfo.token, 	//token
visitCardNo:this.cradNumber, 	// 就诊卡号
visitorBm:this.fromJzr ? this.jzrInfo.CBM : this.usercode, // 就诊人编码
cardTypeBm:this.fromJzr ? this.jzrInfo.CZJLX : commonFn.getIdcardTypeBM(cardType), 	// 证件类型编码
cardTypeMc:this.fromJzr ? this.jzrInfo.CZJLXMC : cardType, 	// 证件类型名称
cardNo:this.fromJzr ? this.jzrInfo.CZJHM : this.idcard, 	// 证件类号码
visitCardTypeBm:'1', 	// 卡片类型编码
hospitalBm:this.hospitalBM, 	// 医院编码
}).then(res => {
let THIS = this;
if(typeof res != 'undefined' && typeof res.status != 'undefined' && res.status == true){

this.$vux.toast.show({
text: '添加成功!',
type: 'success',
onHide(){
THIS.$router.goBack();
}
});

}


}).catch(error => {
console.log(error);
})
}
}
}



