export default{
	getIdcardTypeBM(str){	// 获取证件类型编码
		switch (str) {
			case '居民身份证':
				return '01' 
				break;
			case '居民户口簿':
				return '02' 
				break;
			case '护照':
				return '03' 
				break;
			case '军官证(士兵证)':
				return '04' 
				break;
			case '驾驶执照':
				return '05' 
				break;
			case '港澳居民来往内地通行证':
				return '06' 
				break;
			case '台湾居民来往内地通行证':
				return '07' 
				break;
			case '其他':
				return '99' 
				break;

			default:
				return '';
				break;
		}
	},
	getIdcardTypeMC(str){	// 获取证件类型名称
		switch (str) {
			case '01':
				return '居民身份证' 
				break;
			case '02':
				return '居民户口簿' 
				break;
			case '03':
				return '护照' 
				break;
			case '04':
				return '军官证(士兵证)' 
				break;
			case '05':
				return '驾驶执照' 
				break;
			case '06':
				return '港澳居民来往内地通行证' 
				break;
			case '07':
				return '台湾居民来往内地通行证' 
				break;
			case '99':
				return '其他' 
				break;

			default:
				return '';
				break;
		}
	},

	getGenterBM(str){	// 获取性别编码
		switch (str) {
			case '未知性别':
				return '0' 
				break;
			case '男':
				return '1' 
				break;
			case '女':
				return '2' 
				break;
			case '未说明性别':
				return '9' 
				break;

			default:
				return '';
				break;
		}
	},
	getGenterMC(str){	// 获取性别名称
		switch (str) {
			case '0':
				return '未知性别' 
				break;
			case '1':
				return '男' 
				break;
			case '2':
				return '女' 
				break;
			case '9':
				return '未说明性别' 
				break;

			default:
				return '';
				break;
		}
	}

}



